<template>
	<div id="outer">
		<div id="top">
			<v-btn color="green">Nieuw</v-btn>
		</div>
		<v-list subheader id="list">
			<!-- <v-subheader>Ingrediënten</v-subheader> -->
			<v-list-tile
			v-for="item in items"
			:key="item.id"
			avatar
			@click=""
			>
				<v-list-tile-avatar>
					<v-icon>fa-hamburger</v-icon>
				</v-list-tile-avatar>

				<v-list-tile-content>
					<v-list-tile-title v-html="item.name"></v-list-tile-title>
				</v-list-tile-content>

				<v-list-tile-action>
					<!-- <v-icon color="teal">chat_bubble</v-icon> -->
				</v-list-tile-action>
			</v-list-tile>
		</v-list>
	</div>
</template>

<script>
export default {
	data () {
		return {
			items: [
                { id: 0, name: 'Pasta rode saus' },
				{ id: 1, name: 'Pasta witte saus' },
				{ id: 2, name: 'Falafel' },
				{ id: 3, name: 'Hamburger op brood' },
				{ id: 4, name: 'Patat' },
				{ id: 5, name: 'Pannenkoeken' },
				{ id: 6, name: 'Wraps' },
				{ id: 7, name: 'Gado gado' }
			]
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#outer {
	height: 100%;
}

#top {
	text-align: left;
}

#list {
	height: calc(100% - 60px);
	overflow-y: auto;
}
</style>



